import React , {useEffect, useState} from 'react'
import ChatHome from '../components/chat-home/ChatHome'
import LinksHome from '../components/links-home/LinksHome'
import Header from '../components/Header/Header'
import "../styles/Towatch.css"

const Towatch = () => {
    const[data,setData]=useState([])
    const [page, setPage] = useState(1);

    useEffect(()=>{
      try{
        const fetchData = async () => {
          try {
              const response = await fetch(`https://creen-program.com/api/videos?page=${page}`, {
                  method: 'post',
                  headers: {
                      'Content-type': 'application/json; charset=UTF-8',
                      'accept': 'application/json'
                  },
              });

              if (!response.ok) {
                  throw new Error(`HTTP error! Status: ${response.status}`);
              }

              const newData = await response.json();
              console.log(newData.data.videos.data)
              setData((prevData) => [...prevData, ...newData.data.videos.data]);
          } catch (error) {
              console.error('Error fetching data:', error);
          }
      };        
      fetchData()
      }catch(error){}
    },[page])
    const handleScroll = () => {
      if (
          window.innerHeight + document.documentElement.scrollTop + 1 >= document.documentElement.scrollHeight
      ) {
          setPage((prevPage) => prevPage + 1);
      }
  };

  useEffect(() => {
      window.addEventListener('scroll', handleScroll);
      return () => window.removeEventListener('scroll', handleScroll);
  }, []);
console.log(data)
    
  return (
    <>
    <Header/>
    <div className="towatch" style={{display:"flex",justifyContent:"space-between"}}>
        <ChatHome/>
        <section style={{width:"100%",display:"flex",justifyContent:"center",alignItems:"center",gap:"10px",flexDirection:"column"}}>
            <div className="head" style={{width:"75%"}}>
                <span style={{display:"flex",justifyContent:"end",alignItems:"center"}}>
                    <span style={{    fontSize: "20px",fontFamily: 'remixicon'}}>يشاهد</span>
                    <span><i class="ri-film-fill" style={{    fontSize: "1.2rem",color: "#c64d53"}}></i></span>
                    </span>
                    </div>
                    {
                            data.map((data , index)=>(
                              <div className="post" key={index} style={{width:"75%"}}>
                                <div className="header">
                              <div className="left">
                                <span><i class="ri-arrow-down-s-line" style={{fontSize:"1.2rem"}}></i></span>
                              </div>
                              <div className="right">
                                <div className="left">
                                  <span><b>{data.user.name}</b></span>
                                  <div className="time">
                                    <span style={{direction:"rtl"}}>22ساعة</span>
                                    <span className='privacy'>
                                    <i class="ri-global-line"></i>
                                    <span className='arrow'></span>
                                    </span>
                                  </div>
                                </div>
                                <div className="right">
                                  <img src={data.user.profile} alt="" />
                                </div>
                              </div>
                            </div>
                            
                            <div className='content' style={{    lineBreak: "anywhere"}}>
                             {
      <video width="100%" style={{height:"88vh"}} controls >
      <source src={data.url} type="video/mp4"/>
     </video>
                             }
                             </div>
                          <div className="add_comment">
                            <input type="text" placeholder="اكتب تعليق وأضغط أنتر .." />
                            <img src={data.user.profile} alt="" />
                          </div>
                          <div className="send_comment">
                            <span><i class="ri-send-plane-2-line"></i></span>
                            <div className="file">
                              <span><i class="ri-image-2-line"></i></span>
                              <input type="file" />
                            </div>
                          </div>
                                                  </div>

                            
                            ))
                          }
        </section>
        <LinksHome/>
            </div>
    </>
  )
}

export default Towatch