import React, { useEffect, useState }  from 'react'
import Header from "../components/Header/Header"
import { Link } from 'react-router-dom'
import "../styles/products.css"
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/navigation';
import { Navigation } from 'swiper/modules';
import ProductsCard from '../components/Productcard/ProductsCard';
import axios from 'axios';
//import axios from 'axios';


const Products = () => {
    const [data , setData] = useState([])
    const [dataCategory , setDataCategory] = useState([])
    useEffect(()=>{
        try{
       
        const categoryData = async () => {
            try {
                const response = await axios.post(`https://creen-program.com/api/products/categories`, {
                    
                    headers: {
                        'Content-type': 'application/json; charset=UTF-8',
                        'accept': 'application/json'
                    },
                });
      
                
      
            
                setDataCategory(response.data.data.categories.data);
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };
        categoryData()
        }catch(error){}
       })
  
  return (

    <>
    <Header/>
    <section className='products_and_ads'>
        <div className="products_left">a</div>
        <div className="products_center">
            <div className="products_center_head">
                <Link>أحدث المنتجات</Link>
                <Link>أكثر المبيعات</Link>
                <Link>أخترنا لك</Link>
                <Link>العروض</Link>
                <Link>منتجات المزاد</Link>
            </div>
            <div className="ads" style={{    marginTop: "10px",
    display: "none",flexDirection:"column",gap:"10px"}}>
                <div style={{  display:"none",width: "100%"}}>
                    {
                        //data.map((item , index)=>(
                          //  <Link key={index}><img src={item.image} alt="" style={{height:"400px",borderRadius:"10px"}} /></Link>
                        //))
                    }
                </div>
                <div style={{    display: "none",justifyContent: "space-between",gap: "10px"}}>
                <div style={{    background: "red",height: "100px",width: "20%"}}></div>
                <div style={{    background: "red",height: "100px",width: "20%"}}></div>
                <div style={{    background: "red",height: "100px",width: "20%"}}></div>
                <div style={{    background: "red",height: "100px",width: "20%"}}></div>
                <div style={{    background: "red",height: "100px",width: "20%"}}></div>
                </div>
            </div>
            <div className="slider" style={{marginTop:"15px"}}>
            <Swiper
      // install Swiper modules
      modules={[Navigation]}
      spaceBetween={50}
      slidesPerView={3}
      navigation
      pagination={{ clickable: true }}
      scrollbar={{ draggable: true }}

    >
      {
        dataCategory.map((item , index)=>(
            <SwiperSlide key={index}>{item.name}</SwiperSlide>
        ))
      }
    
    </Swiper>

            </div>
            <ProductsCard/>
           
            
        </div>
        <div className="products_right">c</div>
    </section>
    </>
  )
}

export default Products