import React , {useState} from 'react'
import Header from '../components/Header/Header'
import "../styles/Wallet.css"
import LinksHome from '../components/links-home/LinksHome'
import ChatHome from '../components/chat-home/ChatHome'
import visa from "../assets/images/visa.png"
import mada from "../assets/images/mada.jpg"
import stc from "../assets/images/photo_2022-10-21_19-08-07.jpg"
import masterCard from "../assets/images/mastercard.png"
const Wallet = () => {
    const[tab , settab]=useState('points');
    const [active , setactive]=useState('');

  return (
    <>
    <Header/>
<section style={{width:"100%",display:'flex',justifyContent:"space-between",marginTop:"10px"}}> 
    <ChatHome/>
<section style={{width:"50%"}}>
    <div className="wallet">
    <div className="container">
        <div className="info">
            <div className="user">
            <div className="logo">
                <span><i class="ri-wallet-fill" style={{fontSize: "2rem",color: "#00cb00"}}></i></span>
            </div>
            <div className="info-user">
                <span>username</span>
                <img src="" alt="" />
            </div>

            </div>
            <div className="Available-Balance" style={{paddingBottom:"20px"}}>
            <p style={{fontSize:"larger"}}>الرصيد المتوفر</p>
            <span style={{fontSize:"larger"}}>3980.75<span>ريال سعودي</span></span>
        </div>            
                    </div>

        <div className="buttons">
            <div className="points"><button onClick={()=>settab('points')}></button></div>
            <div className="withdraw">
                <button onClick={()=>settab('withdraw')}>
                    <i class="ri-refund-fill" style={{fontSize:"1.2rem"}}></i>
                    </button>
                    <span>سحب</span>
                    </div>
            <div className="shipping">
                <button onClick={()=>settab('shipping')}>
                    <i class="ri-bank-card-fill" style={{fontSize:"1.2rem"}}>
                        </i></button>
                        <span>شحن</span>
                        </div>
        </div>
        <div className="content" style={{textAlign:"end"}}>
            {
                tab === 'points'?
                <>
                <div className="balance">
                <h3>تحويل النقاط كرصيد للمحفظة</h3>
                <h4 style={{padding:"10px"}}>عدد النقاط</h4>
                <input type="text" placeholder='عدد النقاط'/>
                <button>تحويل</button>
                </div>
                <div className="gifts">
                <h3>تحويل النقاط الي هدايا</h3>
                <h4 style={{padding:"10px"}}>عدد النقاط</h4>
                <input type="text" placeholder='عدد النقاط'/>
                <button>تحويل</button>
                </div>
                <div className="all-balance" style={{paddingTop:"5px"}} >
                    <h3>الرصيد الكلي:</h3>
                </div>
                
                </>:tab==="withdraw"?
                
            <div className="withdraw">
            <h3>سحب الرصيد</h3>
                <h4 style={{padding:"10px"}}>المبلغ</h4>
                <input type="text" placeholder='ريال سعودي' />
                <button>سحب</button>
                </div>
                :tab==="shipping"?<div className="shipping">
                    <h3 style={{borderBottom:"1px solid #ddd" , paddingBottom:"5px"}}>شحن المحفظه</h3>
                <div className="payment-method">
                    <img src={stc} alt="" className={ `${active==='stc'?'payment_active': ''} `} onClick={()=>setactive('stc')}/>
                    <img src={masterCard} alt="" className={ `${active==='masterCard'?'payment_active': ''} `} onClick={()=>setactive('masterCard')} />
                    <img src={visa} alt="" className={ `${active==='visa'?'payment_active': ''} `} onClick={()=>setactive('visa')} />
                    <img src={mada} alt="" className={ `${active==='mada'?'payment_active': ''} `} onClick={()=>setactive('mada')} />
                </div>
                <h4 style={{padding:"10px"}}>المبلغ</h4>
                <input type="text" placeholder='ريال سعودي' />
                <button>دفع</button>

                </div>:<></>
            }
        </div>
    </div>
</div>
    </section>
    <LinksHome/>
</section>
    </>

    )
}

export default Wallet